
import { defineComponent } from 'vue';

export default defineComponent({
	props: {
		icon: String,
		label: String,
		images: {
			default: [] as string[],
		},
	},
	computed: {
		background(): string {
			return this.images
				.map(i => `center / contain no-repeat url('${i}')`)
				.join(',');
		},
	},
});
